<template>
  <!-- results -->
  <b-tabs
    v-if="shownResult"
    :class="`training-results tabs-count-${results.length}`"
    :modelValue="results.length - 1"
  >
    <b-tab
      v-for="(items, i) in $store.getters.results"
      :key="i"
      tag="table"
      :title="$t(`training.results.tab-${i}`)"
    >
      <tr v-for="(item, j) in items" :key="j" :let="item = item.item || item">
        <td>
          <div class="training-results-text">
            <dit-lang-icon :lang="item.lang" />
            {{ item.text }}
          </div>
        </td>
        <td>
          {{ item.translation }}
        </td>
        <td>{{ item.score }}</td>
      </tr>
    </b-tab>
  </b-tabs>

  <!-- training -->
  <div class="training-desk">
    <keep-alive>
      <training v-if="!shownResult"></training>
    </keep-alive>
  </div>
</template>

<script>
import DitLangIcon from '@/components/DitLangIcon/DitLangIcon';
import Training from '@/views/Training/Training';

export default {
  name: 'Bulk Training',
  inject: ['buttons'],
  components: {
    DitLangIcon,
    Training,
  },
  data() {
    return {
      shownResult: false,
    };
  },
  computed: {
    isSuccess() {
      return this.$store.getters.isSuccess;
    },
    item() {
      return this.$store.getters.trainingItem;
    },
    results() {
      return this.$store.getters.results;
    },
  },
  watch: {
    '$store.getters.settings.updated'() {
      switch (this.$store.getters.settings.lastUpdated) {
        case 'textType':
        case 'lang':
        case 'native':
        case 'useReverseTranslation':
          this.$store.commit('trainingItems');
      }
    },
    '$store.getters.settings.training.count'() {
      this.update();
    },
    '$store.getters.listening.enabled'() {
      this.update();
    },
    'buttons.left.pressed'() {
      this.onPressButton('left');
    },
    'buttons.right.pressed'() {
      this.onPressButton('right');
    },
    '$store.getters.trainingType'() {
      this.setButtons();
      this.shownResult = false;
    },
    isSuccess() {
      this.setButtons();
    },
    item(item) {
      if (!item) return;

      this.setButtons();
      this.shownResult = false;
    },
    shownResult() {
      this.setButtons();
    },
  },
  unmounted() {
    this.$store.commit('alert', { noneItems: undefined });
  },
  methods: {
    next() {
      if (this.item) {
        if (this.shownResult) {
          this.shownResult = false;
          return;
        }

        const successType = this.buttons.right.type;
        if (
          this.isSuccess ||
          successType === 'skip' ||
          successType === 'correct' ||
          successType === 'incorrect' ||
          successType === 'continue'
        ) {
          this.$store.commit('nextItem');
          if (!this.item) this.shownResult = true;
        }
      } else this.update();
    },
    update() {
      if (!this.$store.getters.itemsLength) return; // before dictionary loaded

      this.$store.commit('setBulkOptions');
    },
    setButtons() {
      this.buttons.set(
        this.shownResult
          ? {
              // note: it's a hack for score.exam
              left: {
                type: this.item ? 'continue' : 'restart',
              },
              right: {
                visible: false,
              },
            }
          : {
              left: {
                type: 'results',
              },
              right: {
                type: this.isSuccess
                  ? this.buttons.right.specialType || 'continue'
                  : 'skip',
                visible: true,
              },
            }
      );
    },
    onPressButton(name) {
      switch (this.buttons[name].type) {
        case 'check':
          return;
        case 'results':
          this.shownResult = true;
          return;
        case 'continue':
          if (this.shownResult) {
            this.shownResult = false;
            return;
          }
        // case 'skip':
        // case 'restart':
        // case 'correct':
        // case 'incorrect':
      }
      return this.next();
    },
  },
};
</script>
